img {
  width: 100px;
}

.container {
  background-color: white;
  width: 400px;
  padding: 30px 25px;
  border-radius: 8px;
  box-shadow: 1px 1px 10px rgb(0, 0, 0, 0.2), -1px -1px 10px rgb(0, 0, 0, 0.2);
}

.showCase {
  display: flex;
  align-items: center;
  gap: 20px;
  margin: 20px 0;
}

@media (max-width: 426px) {
  .container {
    width: 300px;
    padding: 10px;
  }
}
